import React from 'react';
import {Nav, Container, Navbar, NavDropdown} from 'react-bootstrap';
import {Link} from 'react-router-dom';


const Navigator = ()=>{
        return(
            <div className='container-fluid footer-bg  footer'>
            <div className='row'> 
                 <div className='col-lg-12 p-2 text-center'>
                         <li> <Link  id='a' to="/">Home </Link>   </li> 
                         <li> <Link  id='a' to="/about">About Us </Link> </li>
                         <li> <Link  id='a' to="/contact">Contact Us </Link> </li>
                         <li> <Link  id='a' to="/gallery">Gallery </Link> </li>
                         
                 </div>
                
             </div> 
     
     </div>)
            
         
}

export default Navigator;